.block--local-tasks-block {
  background: #fff;
  font-family: sans-serif;
  font-weight: normal;
  position: fixed;
  inset: auto 0 0;
  box-shadow: 0 0 10px #0000001a;
}

@media (max-width: 499.98px) {
  .block--local-tasks-block {
    display: none;
  }
}

body.toolbar-hidden .block--local-tasks-block {
  display: none;
}

.block--local-tasks-block ul {
  flex-flow: wrap;
  justify-content: center;
  padding: 0;
  list-style: none;
  display: flex;
}

.block--local-tasks-block li:first-child {
  margin-left: 0;
}

.block--local-tasks-block a {
  color: gray;
  padding: 10px 20px;
  text-decoration: none;
  display: block;
}

.block--local-tasks-block a:hover {
  color: #000;
  background: #f5f5f5;
}

.block--local-tasks-block a.is-active {
  color: #000;
  border-bottom: 2px solid #000;
  padding-bottom: 8px;
}

